.label_playlist {
    width: 75px;
    height: 100%;
    background: rgba(0,0,0,0.8);
    position: absolute;
    top: 0;
    right: 0;
    transition-duration: 400ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    .number_playlist {
        width: 75px;
        text-align: center;
        font-size: 26px;
        color: #fff;
        position: absolute;
        right: 0;
        bottom: 20px;
        opacity: 0.5;
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    }
    .ic_playlist{
        display: block;
        margin: 7px auto 0 auto;
    }
}

.label_viewall {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    font-size: 18px;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition-duration: 400ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
