.navigation_scroll{overflow-x: scroll; overflow-y: hidden; -webkit-overflow-scrolling: touch; white-space: nowrap; -webkit-backface-visibility: hidden; backface-visibility: hidden;}
.navigation_scroll ul{display: table-cell; vertical-align: middle;}
.navigation_scroll ul li{display: table-cell;position: relative;}
.navigation_scroll ul li + li:before{content: '';width: 1px;height: 17px;background: #ccc;position: absolute;right: 0;top: 10px;}
.navigation_scroll ul li.first:before{content: '';width: 10px;height: 1px;background: #d4d4d4;position: absolute;right: -5px;top: 17px;transform: rotate(45deg)}
.navigation_scroll ul li.first:after{content: '';width: 10px;height: 1px;background: #d4d4d4;position: absolute;right: -5px;top: 24px;transform: rotate(-45deg)}
.navigation_scroll ul li.first a{color: #a0224e;font-weight: bold;}
.navigation_scroll ul li:last-child{padding-right: 50px; border-right: none;}
.navigation_scroll ul li a{height: 100%; padding: 0 15px; display: block; font: 400 15px/40px arial; color: #333;}
.navigation_scroll ul li.active a{ font-weight: 700;}
