.search-fullscreen {position: relative;}
.search-fullscreen .search-fullscreen-overlay {position: absolute;top: 23px;left: 50%;width: 100%; padding: 0 40px 0 15px; height: auto;z-index: 2000;visibility: hidden;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translate3d(-50%, -50%, 0);
    -moz-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}
.search-fullscreen .search-fullscreen-bg-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 45px;
    z-index: 1000;
    background: rgba(247, 248, 250, 0.2);
    visibility: hidden;
    opacity: 0;
    transition-duration: 500ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
.search-fullscreen .search-fullscreen-overlay-show {visibility: visible;}
.search-fullscreen .search-fullscreen-overlay .search-fullscreen-overlay-content {background: transparent;opacity: 0;overflow: hidden;
    -webkit-transform: scale(0.7, 1);
    -moz-transform: scale(0.7, 1);
    transform: scale(0.7, 1);
    transition-duration: 500ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
.search-fullscreen .search-fullscreen-overlay-show ~ .search-fullscreen-bg-overlay {
    opacity: 1;
    visibility: visible;
    background: #f4f4f4;
}
.search-fullscreen .search-fullscreen-overlay-show .search-fullscreen-overlay-content {opacity: 1;-webkit-transform: scale(1, 1);-moz-transform: scale(1, 1);transform: scale(1, 1);}
.search-fullscreen .search-fullscreen-input-group {position: relative;}
.search-fullscreen .search-fullscreen-input-group .search-fullscreen-input {outline: none; width: 100%;height: 35px;font-size: 18px; font-weight: 400;color: #333;background: transparent;
    border: none;
    border-radius: 0;
    padding:0;
}
.search-fullscreen .search-fullscreen-input-group .search-fullscreen-input:focus{ color: #9f224e;}
.search-fullscreen .search-fullscreen-close {
    position: absolute;
    top: -3px;
    right: 0;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 30px;
    color: #999;
    text-align: center;
    cursor: pointer;
}
.search-fullscreen .search-fullscreen-close:hover {
    color: #9f224e;
}
